@charset "UTF-8";

/* CSS Document */
html {
  font-size: 62.5%;
  /* 16px * 62.5% = 10px */
  width: 100%;
  box-sizing: border-box;
}

body {
  color: #333;
  /* RGB */
  font-family: "Gill Sans","游ゴシック体",
  YuGothic,
  "游ゴシック",
  "Yu Gothic",
  "メイリオ",
  sans-serif;
  font-weight: 500;
  font-size: 1.6em;
  line-height: 2.4rem;
  text-align: center;
}

section h2 {
  font-size: 2.4rem;
}

a:hover {
  opacity: 0.5;
}

.none {
  display: none;
}

.viewmore {
  text-align: center;
  padding: 27px 25px;
  font-weight: 500;
  background: transparent;
  outline: none !important;
  cursor: pointer;
  transition: all 0.3s ease;
  position: relative;
  display: block;
  border: 2px solid #333;
  z-index: 1;
  max-width: 300px;
  font-size: 2.4rem;
  border-radius: 5px;
  margin: 0 auto 30px;
}

.work-button {
  margin: 0 auto 0;
}

.viewmore:after {
  position: absolute;
  content: "";
  width: 0;
  height: 100%;
  top: 0;
  right: 0;
  z-index: -1;
  background: tomato;
  transition: all 0.3s ease;
}

.viewmore:hover {
  opacity: 1 !important;
  color: #fff !important;
  border: 2px solid tomato;
}

.viewmore:hover:after {
  left: 0;
  width: 100%;
}