.footer {
  background-color:silver;
}

.footer-container {
  height: 200px;
  position: relative;
  max-width: 1160px;
  margin: 0 auto;
}

.scroll-top {
  position: absolute;
  height: 60px;
  right: 20px;
  top: 50%;
  transform: translateY(-50%);
}

.footer-logo {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.footer-sentence {
  color: #fff;
  font-size: 3rem;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}