.card {
  height: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width : calc(100% / 3);
  display: flex;
  flex-flow: column;
  justify-content: center;
  align-items: center;
  vertical-align: bottom;
}

  .card-content {
    padding-top: 40px;
    height: 240px;
    margin-bottom: 70px;
  }

.card-title {
  color: #fff;
  font-size: 30px;
  display: block;
  text-align: center;
  margin: 20px;
}

.card-subtitle {
  color: #fff;
  font-size: 20px;
  display: block;
  text-align: center;
}

.card-image {
  height: 55%;
  margin: 0 auto;
}

.card-lead {
  height: 250px;
  width: 80%;
  font-size: 16px;
  color: #fff;
  margin: 0 auto;
  text-align: left;
}

@media (max-width: 800px) {
  .card {
    height: 100vh;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .card-content {
    padding-top: 40px;
    height: 35%;
    margin-bottom: 0px;
  }

  .card-image {
    height: 50%;
    margin: 0 auto;
  }

  .card-title {
    color: #fff;
    font-size: 24px;
    display: block;
    text-align: center;
  }

  .card-subtitle {
    color: #fff;
    font-size: 16px;
    display: block;
    text-align: center;
  }

  .card-lead {
    width: 80%;
    font-size: 16px;
    color: #fff;
    margin: 0 auto;
    padding-bottom: 40px;
  }
}