.main {
  background-image: url("../images/mainBack.jpg");
}

.top {
  height: calc(100vh - 80px);
  background-color: #69c1be;
  position: relative;
}

.top-back__area {
  position: absolute;
  height: 500px;
  width: 80%;
  top: 35%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.top-back {
  height: 80%;
  width: 100%;
  margin-top: 10px;
}

.top-back img {
  object-fit: contain;
  object-position: center;
  height: 100%;
  width: 100%;
}

.title-area {
  position: relative;
  height: 20%;
  line-height: 50px;
}

.top-title {
  position: absolute;
  font-size: 4rem;
  font-weight: 600;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #fff;
  display: block;
  width: 100%;
}

.title-back {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.top-image {
  position: absolute;
  display: flex;
  justify-content: space-between;
  height: 100%;
  width: 100%;
}

.top-image__left {
  background-image: url("../images/worktop_left.png");
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: 0% 100%;
  background-size: contain;
}
.top-image__right {
  background-image: url("../images/worktop_right.png");
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
  background-position: 100% 100%;
  background-size: contain;
}

@media(max-width: 600px) {
  .top {
    height: calc(100vh - 50px);
  }

  .top-back__area {
    position: absolute;
    height: 50%;
    width: 90%;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 10;
  }

  .top-back {
    background-position: center;
    background-repeat: no-repeat;
    background-size: contain;
    height: 80%;
    width: 100%;
    margin-top: 30px;
  }

  .title-area {
    position: relative;
    height: 20%;
    
  }

  .top-back img {
    object-fit: contain;
    object-position: center;
    height: 100%;
    width: 100%;
  }

  .top-title {
    position: absolute;
    font-size: 2.4rem;
    font-weight: 600;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: #fff;
    display: block;
    width: 100%;
  }

  .title-back {}

  .top-image {
    position: absolute;
    display: flex;
    justify-content: space-between;
    height: 100%;
    width: 100%;
    flex-flow: column;
  }

  .top-image__left {
    background-image: url("../images/worktop-top.png");
    background-position: 100% 0%;
    height: 60%;
  }

  .top-image__right {
    background-image: url("../images/worktop-bottom.png");
    height: 100%;
    width: 100%;
    background-repeat: no-repeat;
    background-position: 100% 100%;
    background-size: contain;
    z-index: 20;
  }
}
.items-container {
  padding: 100px 16px;
  max-width: 900px;
  margin: 0 auto;
}

.items {
  
}

.items li {
  margin-bottom: 80px;
  text-align: left;
  list-style: none;
}

.item-title__area {
  position: relative;
  margin-bottom: 20px;
}

.item-title {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 2.4rem;
  font-weight: 600;
}

.item-title__back {
  padding-left: 10px;
}

.item-discription {
  line-height: 32px;
  display: block;
  margin-bottom: 40px;
  font-size: 2rem;
}

.item-discription span {
  display: block;
  font-weight: 600;
  margin-bottom: 10px;
}

.icon-list {
  display: flex;
  margin-top: 15px;
}

.icon {
  height: 100px;
  width: 100px;
  object-fit: contain;
}

.banner {
  height: 500px;
  width: 500px;
}

.banner img {
  height: 100%;
}

@media(max-width: 600px) {
.item-discription {
  line-height: 24px;
  display: block;
  margin-bottom: 30px;
  font-size: 1.6rem;
} 

  .banner {
    height: 100%;
    width: 100%;
  }

  .banner img {
    height: 100%;
    width: 100%;
  }
}