.about-main {
  background-image: url("../images/mainBack.jpg");
}

.about-top {
  height: calc(100vh - 80px);
  background-color: #69c1be;
  position: relative;
}

.about-top__image {
  background-image: url("../images/top.png");
  background-position: 53% 50%;
  background-size: contain;
  background-repeat: no-repeat;
  height: 95%;
  margin: 0 auto;
}

.about-top__message {
  text-align: left;
  color: #fff;
  font-size: 8rem;
  position: absolute;
  top: 10%;
  left: 18%;
  line-height: 90px;
}

.about-top__title {
  text-align: left;
  color: #fff;
  font-size: 7rem;
  position: absolute;
  bottom: 20%;
  right: 23%;
  line-height: 70px;
}



@media(max-width: 600px) {
  .about-top {
    height: calc(100vh - 50px);
    background-color: #69c1be;
    position: relative;
  }

  .about-top__image {
    background-position: 41% 50%;
    background-size: 150%;
  }

  .about-top__message {  
    font-size: 5.5rem;
    top: 15%;
    left: 8%;
    line-height: 70px;
  }

  .about-top__title {
    text-align: left;
    color: #fff;
    font-size: 4rem;
    position: absolute;
    bottom: 27%;
    right: 8%;
    line-height: 50px;
  }


}




.profile-container {
  max-width: 1050px;
  padding: 0 24px;
  margin: 0 auto;
  /* color: #707070;  */
}

.profile-title {
  padding: 100px 0 0 0;
  font-size: 4rem;
  text-align: center;
  font-weight: 600;
}

.profile-title__sub {
  padding: 25px 0 100px;
  font-size: 2.5rem;
  text-align: center;
}

.profile-discription ul {
  display: flex;
  text-align: left;
  margin-bottom: 30px;
  justify-content: space-between;
}

.profile-discription ul li {
  list-style: none;
  text-align: left;
}

.profile-name {
  width: 350px;
}

.profile-name h3 {
  font-size: 3rem;
  display: block;
  letter-spacing: 5px;
  width: 100%;
  text-align: right;
}

.profile-name p {
  font-weight: 500;
  font-size: 20px;
  letter-spacing: 9px;
  text-align: right;
  margin-bottom: 15px;
}

.profile-lead {
  width: 55%;
  margin-left: 30px;
  border-left: solid 5px #aeaeae;
  padding-left: 50px;
  padding-top: 30px;
}

.profile-sml__title {
  display: inline-block;
  font-size: 2.4rem;
  font-weight: 600;
  padding-bottom: 30px;
}

.profile-lead span {
  display: inline-block;
  padding-left: 20px;
  font-size: 2rem;
}

.profile-lead p {
  font-size: 2rem;
  padding-bottom: 50px;
  line-height: 3rem;
}

/* .skill-container {
  max-width: 1050px;
  margin: 0 auto;
} */

@media (max-width: 750px) {
  .profile-container {
    max-width: 1050px;
    margin: 0 auto;
    padding: 0 16px;
    /* color: #707070;  */
  }

  .profile-title {
    padding: 70px 0 0 0;
    font-size: 3rem;
    text-align: center;
    font-weight: 600;
  }

  .profile-title__sub {
    padding: 15px 0 70px;
    font-size: 2rem;
    text-align: center;
  }

  .profile-container ul {
    display: flex;
    flex-flow: column;
    text-align: left;
    margin-bottom: 30px;
    justify-content: space-between;
  }

  .profile-name {
    width: 350px;
    
  }

  .profile-name h3 {
    font-size: 2.4rem;
    padding: 0;
    display: block;
    letter-spacing: 5px;
    position: relative;
    width: 100%;
    text-align: left;
    margin-bottom: 50px;
  }

  .profile-name p {
    font-weight: 500;
    font-size: 20px;
    letter-spacing: 9px;
    text-align: right;
    margin-bottom: 15px;
    text-align: left;
  }

.profile-lead {
  width: 100%;
  margin-left: 0px;
  border-left: none;
  padding-left: 0px;
}

.profile-sml__title {
  display: inline-block;
  font-size: 2rem;
  font-weight: 600;
  padding-bottom: 30px;
}

.profile-lead span {
  display: inline-block;
  padding-left: 20px;
  font-size: 1.6rem;
}

.profile-lead p {
  font-size: 1.6rem;
  padding-bottom: 50px;
  line-height: 3rem;
}
}

