.skill-title__container {
  /* background-color: rgba(0, 0, 0, 0.40); */
  height: 120px;
  margin-top: 120px;
}

.skill-title {
  padding-top: 25px;
  font-size: 4rem;
  text-align: center;
  font-weight: 600;
}

.skill-title__sub {
  padding: 25px 0 0;
  margin-bottom: 15px;
  font-size: 2.5rem;
  text-align: center;
}

.skill-cards {
  display: flex;
  width: 100%;
  height: 100vh;
  flex-wrap: nowrap;
}

@media (max-width: 800px) {
.skill-title__container {
  height: 120px;
  margin-top: 70px;
}

.skill-title {
  padding-top: 25px;
  font-size: 3rem;
  text-align: center;
  font-weight: 600;
}

.skill-title__sub {
  padding: 15px 0 0;
  margin-bottom: 15px;
  font-size: 2rem;
  text-align: center;
}

  .skill-cards {
    display: flex;
    width: 100%;
    height: auto;
    flex-flow: column;
  }
}