.works-main {
  background-image: url("../images/mainBack.jpg");
}

.works-top {
  height: calc(100vh - 80px);
  background-color: #69c1be;
  position: relative;
}

.works-top__image {
  background-image: url("../images/top.png");
  background-position: 53% 50%;
  background-size: contain;
  background-repeat: no-repeat;
  height: 95%;
  margin: 0 auto;
}

.works-top__message {
  text-align: left;
  color: #fff;
  font-size: 8rem;
  position: absolute;
  top: 10%;
  left: 18%;
  line-height: 90px;
}

.works-top__title {
  text-align: left;
  color: #fff;
  font-size: 7rem;
  position: absolute;
  bottom: 20%;
  right: 23%;
  line-height: 70px;
}

@media(max-width: 600px) {
  .works-top {
    height: calc(100vh - 50px);
    background-color: #69c1be;
    position: relative;
  }

  .works-top__image {
    background-position: 41% 50%;
    background-size: 150%;
  }

  .works-top__message {
    font-size: 5.5rem;
    top: 15%;
    left: 8%;
    line-height: 70px;
  }

  .works-top__title {
    text-align: left;
    color: #fff;
    font-size: 4rem;
    position: absolute;
    bottom: 27%;
    right: 8%;
    line-height: 50px;
  }
}

.works-container {
  max-width: 1050px;
  margin: 0 auto;
}

.works {
  margin-top: 70px;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.work {
  list-style: none;
  width: 327px;
  margin-bottom: 50px;
}

.work:last-child {
  margin-bottom: 100px;
}

.work-title {
  font-size: 2rem;
  padding-top: 20px;
  line-height: 32px;
}

.work-image__area {
  width: 327px;
  height: 327px;
  overflow:hidden;
}

.work-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.work-image:hover {
  transform: scale(1.2, 1.2);
  transition: 1s all;
}


.tags {
  display: flex;
  flex-wrap: wrap;
  margin-top: 15px;
  justify-content: space-around;
}

.tag {
  list-style: none;
  background-color: rgba(0, 0, 0, 0.60);
  padding: 7px 15px;
  border-radius: 5px;
  color: #fff;
}