.header {
  height: 50px;
  background-color: #69c1be;
  position: relative;
  color: #fff;
}

.header-container {
  max-width: 1160px;
  margin: 0 auto;
  position: relative;
}

.header-navigation {
  display: block;
  text-align: right;
}

.header-navigation__items {
  padding-top: 20px;
  margin-right: 16px;
}

.header-navigation__item {
  display: inline-block;
  margin-left: 20px;
}

.logo {
  position: absolute;
  left: 16px;
}

.logo:hover {
  opacity: 1 !important;
}

.header-navigation__item a {
  text-decoration: none;
  font-size: 1.6rem;
  font-weight: 600;
  color: #fff;
}

@media (min-width: 600px) {
  .header {
    height: 80px;
  }

  .header-container {
    max-width: 1160px;
    margin: 0 auto;
    position: relative;
  }

  .header-navigation {
    display: block;
    text-align: right;
  }

  .header-navigation__items {
    padding-top: 25px;
  }

  .header-sns_icon {
    vertical-align: bottom;
  }

  .header-navigation__item {
    display: inline-block;
    margin-left: 40px;
  }

  .header-navigation__item a {
    text-decoration: none;
    font-size: 2.4rem;
    font-weight: 600;
    color: #fff;
  }
}