.home-main {
background-image: url("../images/mainBack.jpg");
}

.home-top {
  height: calc(100vh - 80px);
  background-color: #69c1be;
  position: relative;
}

.home-top__image {
  background-image: url("../images/top.png");
  background-position: 53% 50%;
  background-size: contain;
  background-repeat: no-repeat;
  height: 95%;
  margin: 0 auto;
}

.home-top__message {
  text-align: left;
  color: #fff;
  font-size: 8rem;
  position: absolute;
  top: 10%;
  left: 18%;
  line-height: 90px;
}

.home-top__title {
  text-align: left;
  color: #fff;
  font-size: 7rem;
  position: absolute;
  bottom: 20%;
  right: 23%;
  line-height: 70px;
}

.home-container {
  margin: 50px auto 0;
  max-width: 900px;
  padding: 0 24px 100px;
}

.home-container p {
  line-height: 8rem;
  text-align: center;
  font-size: 2.4rem;
  margin-bottom: 50px;
}


.home-container p span {
  font-size: 4rem;
}



@media(max-width: 600px) {
  .home-top {
    height: calc(100vh - 50px);
    background-color: #69c1be;
    position: relative;
  }

  .home-top__image {
    background-position: 41% 50%;
    background-size: 150%;
  }

  .home-top__message {
    font-size: 5.5rem;
    top: 15%;
    left: 8%;
    line-height: 70px;
  }

  .home-top__title {
    text-align: left;
    color: #fff;
    font-size: 4rem;
    position: absolute;
    bottom: 27%;
    right: 8%;
    line-height: 50px;
  }

  .home-container {
    margin: 50px auto;
    padding: 0 16px 60px;
  }

  .home-container p {
    line-height: 7rem;
    text-align: left;
    font-size: 1.6rem;
  }


  .home-container p span {
    font-size: 2.4rem;
  }
}

